import React from "react";
import { Link } from "react-router-dom";
import { ToastNotification } from "../../../components/helpers/notification";
import { Panel, PanelHeader } from "../../../components/panel/panel";
import { getData2, postData, postData2 } from "../../../config/axios";
import Tabel from "../../../components/helpers/tabel";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import {
  getDataBaki,
  getDataBakiByGudang,
} from "../../../actions/datamaster_action";
import { getDataGudang } from "../../../actions/datamaster_action";
import {
  ReanderSelect,
  SkeletonLoading,
} from "../../../components/helpers/field";
// import { getListBarangBarcode } from "../../../actions/databarang_action";
import { setItem } from "../../../components/helpers/function";
import { Loading } from "../../../components/helpers/library";

class CetakBarcode extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);

    this.state = {
      activeTab: "1",
      loading: false,
      isLoading: false,
      kode_baki: "",
      kode_gudang: "",
      databarang: [],
      columns: [
        {
          dataField: "kode_barcode",
          text: "Kode Barcode ",
        },
        {
          dataField: "nama_barang",
          text: "Nama Barang ",
        },
        {
          dataField: "kode_dept",
          text: "Kode Jenis ",
        },
        {
          dataField: "berat",
          text: "Berat ",
        },
        {
          dataField: "kode_intern",
          text: "Kode Intern ",
        },
      ],
    };
  }

  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab,
      });
    }
  }
  componentDidMount() {
    setItem("cetak_barcode_selected", []);
    this.props.dispatch(getDataGudang());
    this.props.dispatch(getDataBaki());
  }
  setSystem(data) {
    let hasil = {
      key: "kategori_harga",
      value: data,
      type: "kategori_harga",
    };
    postData2("para-system/", hasil).then(() => {
      ToastNotification("success", "Berhasil Setting System");
    });
  }

  getBarcode() {
    // this.props.dispatch(getListBarangBarcode(this.state.kode_gudang, this.state.kode_baki))
    this.setState({
      isLoading: true,
    });
    getData2(
      `tambah-barang?kode_gudang=${this.state.kode_gudang || "ALL"}&kode_toko=${
        this.state.kode_baki || "ALL"
      }`
    )
      .then((res) => {
        // console.log(res.data);
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
          this.setState({
            isLoading: false,
            databarang: [],
          });
        } else {
          ToastNotification("success", "Data Barang Tersedia");
          this.setState({
            isLoading: false,
            databarang: res.data,
          });
        }
      })
      .catch((err) => {
        // console.log(err);
        ToastNotification("info", "Terjadi Kesalahan Saat Request Data");
        this.setState({
          isLoading: false,
          databarang: [],
        });
      });
  }

  print() {
    let hasil = JSON.parse(localStorage.getItem("cetak_barcode_selected")) || [];
    if (hasil.length === 0) {
      ToastNotification("info", "Silahkan pilih barcode terlebih dahulu");
    } else {
    postData("tmp-barang", hasil)
      .then((res) => {
        // console.log("");
        ToastNotification("success", "Data Berhasil Disimpan");
      })
      .catch((err) => {
        ToastNotification("info", "Data Gagal Disimpan");
      }); 
      // let panjang = hasil.length;
      let hasil_print = ``;

      // console.log(hasil);
      let no = 1;
      hasil.map((data, index) => {
        if (hasil.length === 1) {
          hasil_print = hasil_print + data.kode_barcode;
        } else {
          if (no === hasil.length) {
            hasil_print = hasil_print + data.kode_barcode;
          } else {
            hasil_print = hasil_print + data.kode_barcode + ',';
          }
        }

      
        no++;

      //   if (index % 2 === 0) {
      //     hasil_print =
      //       hasil_print + `ACB+19ZACD200,3300ZAEX0ARA3H001V001CS2#E4APM0PO3+00YE1IG1PH0A101920610ZAPS`;
      //     hasil_print = hasil_print + `%0H0470V005L0101P02S${data.nama_barang}`;
      //     hasil_print = hasil_print + `%0H0490V0025BG01045>I${data.kode_barcode}`;
      //     hasil_print = hasil_print + `%0H0490V0075L0101P02S${data.kode_barcode}`;
      //     hasil_print = hasil_print + `%0H0470V0100L0101P02S${data.kode_dept}`;
      //     hasil_print = hasil_print + `%0H0470V0115L0102P02M${data.berat}Gr`;
      //     hasil_print = hasil_print + `%0H0470V0165L0101P02S${data.kode_intern}`;
      //   } else {
      //     hasil_print = hasil_print + `%0H0016V0005L0101P02S${data.nama_barang}`;
      //     hasil_print = hasil_print + `%0H0030V0025BG01045>I${data.kode_barcode}`;
      //     hasil_print = hasil_print + `%0H0030V0075L0101P02S${data.kode_barcode}`;
      //     hasil_print = hasil_print + `%0H0016V0100L0101P02S${data.kode_dept}`;
      //     hasil_print = hasil_print + `%0H0016V0115L0102P02M${data.berat}Gr`;
      //     hasil_print = hasil_print + `%0H0016V0165L0101P02S${data.kode_intern}`;
      //     hasil_print = hasil_print + `A/CK1Q1CL0Z`;
      //   }
        return true;
      });
      // if (panjang % 2 === 1) {
      //   hasil_print = hasil_print + `A/CK1Q1CL0Z`;
      // }
      // console.log(hasil_print);
      document.getElementById("barcode").value = hasil_print;
        this.TextFilenotapesanan();
    }
  }
  TextFilenotapesanan() {
    const element = document.createElement("a");
    const file = new Blob([document.getElementById("barcode").value], {
      type: "text/plain;charset=utf-8",
    });
    element.href = URL.createObjectURL(file);
    element.download = "autoprint_barcode.txt";
    document.body.appendChild(element);
    element.click();
  }

  render() {
    let dataGudang = [
      {
        value: "ALL",
        name: "SEMUA",
      },
    ];
    this.props.dataGudang.forEach((list) => {
      let data = {
        value: list.kode_gudang,
        name: list.kode_gudang + " - " + list.nama_gudang,
      };
      dataGudang.push(data);
    });
    let databaki = [
      {
        value: "ALL",
        name: "SEMUA",
      },
    ];
    this.props.databaki.forEach((list) => {
      let data = {
        value: list.kode_baki,
        name: list.kode_baki + " - " + list.nama_baki,
      };
      databaki.push(data);
    });
    const selectedRow = {
      mode: "checkbox",
      clickToSelect: true,
      selectColumnPosition: "left",
      onSelect: (row, isSelect, rowIndex, e) => {
        // console.log(isSelect);
        if (isSelect === true) {
          let hasil = {
            kode_barcode: row.kode_barcode,
            nama_barang: row.nama_barang,
            berat: row.berat,
            kode_intern: row.kode_intern,
            kode_dept: row.kode_dept,
            // kadar_cetak : row.kadar_cetak,
          };
          let cek =
            JSON.parse(localStorage.getItem("cetak_barcode_selected")) || [];
          let hasil_cek = cek.findIndex(
            (hasil) => hasil.kode_barcode === row.kode_barcode
          );
          if (hasil_cek === -1) {
            cek.push(hasil);
            localStorage.setItem("cetak_barcode_selected", JSON.stringify(cek));
          }
        } else {
          // console.log();
          if (localStorage.getItem("cetak_barcode_selected") === null) {
          } else {
            let cek =
              JSON.parse(localStorage.getItem("cetak_barcode_selected")) || [];
            let hasil_cek = cek.findIndex(
              (hasil) => hasil.kode_barcode === row.kode_barcode
            );
            let data = JSON.parse(
              localStorage.getItem("cetak_barcode_selected")
            );
            data.splice(hasil_cek, 1);
            localStorage.setItem(
              "cetak_barcode_selected",
              JSON.stringify(data)
            );
          }
        }
      },
      onSelectAll: (isSelect, data, e) => {
        data.forEach((list, index) => {
          if (isSelect === true) {
            let hasil = {
              kode_barcode: list.kode_barcode,
              nama_barang: list.nama_barang,
              berat: list.berat,
              kode_intern: list.kode_intern,
              kode_dept: list.kode_dept,
              kadar_cetak: list.kadar_cetak,
            };
            let cek =
              JSON.parse(localStorage.getItem("cetak_barcode_selected")) || [];
            let hasil_cek = cek.findIndex(
              (hasil) => hasil.kode_barcode === list.kode_barcode
            );
            if (hasil_cek === -1) {
              cek.push(hasil);
              localStorage.setItem(
                "cetak_barcode_selected",
                JSON.stringify(cek)
              );
            }
          } else {
            if (localStorage.getItem("cetak_barcode_selected") === null) {
            } else {
              let cek =
                JSON.parse(localStorage.getItem("cetak_barcode_selected")) ||
                [];
              let hasil_cek = cek.findIndex(
                (hasil) => hasil.kode_barcode === list.kode_barcode
              );
              let data = JSON.parse(
                localStorage.getItem("cetak_barcode_selected")
              );
              data.splice(hasil_cek, 1);
              localStorage.setItem(
                "cetak_barcode_selected",
                JSON.stringify(data)
              );
            }
          }
        });
      },
    };
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Dashboard</Link>
          </li>
          <li className="breadcrumb-item">Cetak Barcode</li>
        </ol>
        <h1 className="page-header">Cetak Barcode</h1>
        <Panel>
          <PanelHeader>Cetak Barcode</PanelHeader>
          <br />
          {/* Blank Page */}
          <div className="container">
            <div className="row">
              <div className="d-none">
                <textarea
                  style={{ display: "none" }}
                  id="barcode"
                  rows="100"
                  cols="100"
                />
              </div>
              <div className="col-3">
                {this.state.loading === false ? (
                  <Field
                    label="Gudang"
                    name="kode_gudang"
                    options={dataGudang}
                    placeholder={"Silahkan Pilih Kode Baki"}
                    onChange={(e) => {
                      if (e !== "ALL") {
                        this.props.dispatch(getDataBakiByGudang(e));
                      } else {
                        this.props.dispatch(getDataBaki());
                      }
                      this.setState({
                        kode_gudang: e,
                      });
                    }}
                    component={ReanderSelect}
                  />
                ) : (
                  <SkeletonLoading label="Kode Baki" />
                )}
              </div>
              <div className="col-3">
                {this.state.loading === false ? (
                  <Field
                    label="Kode Baki"
                    name="kode_baki"
                    options={databaki}
                    onChange={(e) => {
                      this.setState({
                        kode_baki: e,
                      });
                    }}
                    placeholder={"Silahkan Pilih Kode Baki"}
                    component={ReanderSelect}
                  />
                ) : (
                  <SkeletonLoading label="Kode Baki" />
                )}
              </div>
              <div className="col-lg-6">
                <div className="row">
                  <div className="col-lg-6">
                    <label htmlFor="">&nbsp;</label>
                    <button
                      className="btn btn-outline-secondary btn-block"
                      onClick={(data) => this.getBarcode(data)}
                    >
                      {this.state.isLoading ? (
                        <>
                          <i className="fas fa-spinner fa-spin"></i> &nbsp;
                          Sedang Mencari
                        </>
                      ) : (
                        "Cari Data"
                      )}
                    </button>
                  </div>
                  <div className="col-lg-6">
                    <label htmlFor="">&nbsp;</label>
                    <button
                      className="btn btn-primary btn-block"
                      onClick={() => this.print()}
                    >
                      Print Barcode
                    </button>
                  </div>
                </div>
              </div>
            </div>
            <Tabel
              selectRow={selectedRow}
              keyField="kode_barcode"
              columns={this.state.columns}
              data={this.state.databarang || []}
              empty={true}
            />
          </div>
          <br />
        </Panel>
        {/* End Tambah Blank Page  */}
        {this.state.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </div>
    );
  }
}

CetakBarcode = reduxForm({
  form: "CetakBarcode",
  enableReinitialize: true,
})(CetakBarcode);
export default connect((state) => {
  return {
    databaki: state.datamaster.getDataBaki,
    dataGroup: state.datamaster.GetDataGroup,
    dataJenis: state.datamaster.getDataJenis,
    dataGudang: state.datamaster.getDataGudang,
    // barangBarcode : state.databarang.barangBarcode
  };
})(CetakBarcode);
